/**
 * 
 * 1N
 * 2**
 * 3(N)
 * @param fn 
 * @param delay 
 * @param immediate  ()
 */
function debounced(fn, delay = 1000, immediate = false) {
  let timer = null
  return function () {
    if (immediate) {
      //console.log(" ")
      fn.apply(this, arguments)
      immediate = false
      return
    }
    if (timer) {
      //console.log("N")
      clearTimeout(timer)
    }
    timer = setTimeout(() => {
      //console.log("")
      clearTimeout(timer)
      fn.apply(this, arguments)
    }, delay)
  }
}

/**
 * 
 * N
 * @param fn 
 * @param delay 
 * @param immediate  ()
 */
function throttle(fn, delay = 1000, immediate = false) {
  //console.log("")
  let timer;
  let status = false // 
  return function () {
    let _this = this
    let args = arguments

    if (immediate) {
      //console.log(" ")
      fn.apply(_this, args)
      immediate = false
      return
    }
    if (status) {
      //console.log("")
      return
    }
    //console.log("")
    fn.apply(_this, args)
    status = true // 
    timer = setTimeout(() => {
      //console.log(",")
      status = false
    }, delay)
  }
}

export {
  debounced,
  throttle
}